html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	font-family: $main-font-family;
	font-size: $main-font-size;
	line-height: unset;
	margin: 0;
	min-width: 320px;
	background-color: $main-background-color;
	color: $main-color;
	

}

body.modal-active {
	overflow: hidden;
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
	color: inherit;
}

