.deadlines {
	background: linear-gradient(45deg, $grad-color-from, $grad-color-to);
	color: white;
	padding-top: 42px;
	padding-bottom: 42px;
	position: relative;
	@media ($md) {
		padding-top:71px;
    padding-bottom: 77px;
	}
	@media ($lg) {
		padding-bottom: 80px;
	}

	&__text {
		@include reset;
		font-size: 28px;
		line-height: 1.4em;
		@media ($md) {
			font-size: 45px;
		}
		@media ($lg) {
			padding: 0 110px;
			text-indent: unset;
		}
	}
	
	&__comment {
		text-indent: -10px;
		font-size: 18px;
		line-height: 1.65em;
		padding-left: 10px;
		margin-top: 37px;
		margin-bottom: 0; /*обнуление */
		@media ($md) {
			margin-top: 27px;
			font-size: 22px;
			line-height: 1.4em;
			width: 90%;
		}
		@media ($lg) {
			padding: 0 110px;
			text-indent: unset;
		}
	}
}

