.fly-header {
	position: absolute;
	background: linear-gradient(45deg, $grad-color-from, $grad-color-to); 
  -webkit-background-clip: text; /* Обрезка фона по тексту */
	-webkit-text-fill-color: transparent; /* Прозрачный текст */
	font-weight: bold;
	transition: all 2s ease-in;
}

.fly-item1 {
	top: 10%;
	left: 16%;
	font-size: 28px;
	transform: rotate(30deg);
	@media ($md) {
		top: 12%;
		left: 8%;
		font-size: 50px;
	}
	@media ($lg) {
		top: 9%;
		left: 7%;
	}
}

.fly-item2 {
	top: 35%;
	left: 11%;
	font-size: 31px;
	transform: rotate(14deg);
	@media ($md) {
		top: 80%;
		left: 6%;
		font-size: 58px;
	}
	@media ($lg) {
		top: 40%;
		left: 13%;
	}
}

.fly-item3 {
	top: 5%;
	left: 53%;
	font-size: 22px;
	transform: rotate(-35deg);
	@media ($md) {
		top: 73%;
		left: 35%;
		font-size: 43px;
	}
	@media ($lg) {
		top: 85%;
		left: 20%;
	}
}

.fly-item4 {
	top: 21%;
	left: 75%;
	font-size: 17px;
	transform: rotate(20deg);
	@media ($md) {
		top: 10%;
		left: 86%;
		font-size: 34px;
		transform: rotate(-15deg);
	}
	@media ($lg) {
		top: 12%;
		left: 41%;
	}
}

.fly-item5 {
	display: none;
	top: 21%;
	left: 41%;
	font-size: 52px;
	transform: rotate(13deg);
	@media ($md) {
		display: block;
	}
	@media ($lg) {
		display: none;
	}
}

.fly-item6 {
	display: none;
	top: 66%;
    left: 49%;
    font-size: 43px;
		transform: rotate(13deg);
		@media ($lg) {
			display: block;
		}
}

.fly-deadlines {
	position: absolute;
	background: transparent;
	font-weight: bold;
	transition: all 2s ease-in;
}

.fly-item7 {
	display: none;
	top: 3%;
  left: 15%;
  font-size: 40px;
	transform: rotate(35deg);
	
	@media ($lg) {
		display: block;
		top: 15%;
		left: 7%;
		font-size: 47px;
	}
}

.fly-item8 {
	display: none;
	top: 79%;
	left: 50%;
	font-size: 35px;
	transform: rotate(15deg);
	
	@media ($lg) {
		display: block;
		top: 80%;
		left: 74%;
	}
}

.fly-item9 {
	display: none;
	top: 77%;
	left: 84%;
	font-size: 50px;
	transform: rotate(-25deg);
	
	@media ($lg) {
		display: block;
		top: 19%;
		left: 86%;
		font-size: 41px;
	}
}

.fly-item10 {
	display: none;
	top: 68%;
	left: 13%;
	font-size: 61px;
	transform: rotate(26deg);
	
	@media ($lg) {
		display: block;
	}
}

.fly-item11 {
	display: none;
	top: 70%;
	left: 92%;
	font-size: 48px;
	transform: rotate(-19deg);
	
	@media ($lg) {
		display: block;
	}
}


