.examples {
	background-color: #fbfbfb;
	padding-top: 44px;
	padding-bottom: 51px; 
	position: relative;
	@media ($md) {
		margin-top: 0;
    padding-bottom: 100px;
    padding-top: 90px;
	}
	@media ($lg) {
		padding-top: 0;
		padding-bottom: 103px;
	}

	&__title {
		text-align: center;
		font-weight: bold;
		font-size: 30px;
		margin: 0;/* обнуление завода */
		margin-bottom: 40px;
		line-height: 1.2em;
		@media ($md) {
			font-size: 55px;
			margin: 0 0 95px 0;
			letter-spacing: -0.01em;
		}
		@media ($lg) {
			padding-top: 88px;
			margin-bottom: 102px;
		}
	}
}