.do {
	margin-bottom: 51px;
	@media ($md) {
		padding-top: 0;
		margin-bottom: 103px;
	}
}
	
.do__title {
	@include reset;
	text-align: center;
	font-weight: bold;
	font-size: 32px;
	margin-bottom: 42px;
	letter-spacing: -1px;
	@media ($md) {
		margin-bottom: 88px;
		font-size: 54px;
	}
}

.do__list {
	@include reset;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	@media ($md) {
		padding: 0 85px;
	}
	@media ($lg) {
		padding: 0;
		justify-content: space-between;
	}
}

.do__item {
	@include reset;
	max-width: 260px;
	margin-bottom: 51px;
	padding: 0 20px;
	@media ($md) {
		margin-bottom: 77px;
		padding: 0;
	}

	&:nth-child(3){
		@media ($md) {
			margin-bottom:0;
		} 
	}

	&:last-child {
	margin-bottom: 0;
		@media ($md) {
			margin-bottom:0;
		}
	}
}
	
.do__text {
	@include reset;
	padding-top: 75px;
	background-position: 50% 0;
	font-size: 16px;
	color: #8993ad;
	text-align: center;
	background-repeat: no-repeat;
	background-size: 62px 60px;
	line-height: 1.4;

	&_tools {
		background-image: url('data:image/png;base64,');
		@media ($md) {
			@include do-icons-incommon;
		}
	}

	&_responsive {
		background-image: url('data:image/png;base64,');
		@media ($md) {
			@include do-icons-incommon;
		}
	}

	&_programming {
		background-image:url('data:image/png;base64,');
		@media ($md) {
			@include do-icons-incommon;
		}
	}

	&_coding {
		background-image: url('data:image/png;base64,');
		@media ($md) {
			@include do-icons-incommon;
		}
	}
}

