@mixin do-icons-incommon {
  padding-top: 95px;
	font-size: 20px;
	background-size: 74px 74px;
}

@mixin clearfix {
  &::after {
	content: "";
  display: table;
  clear: both;
  }
}
@mixin reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin burger-icon-incommon {
  display: block;
  position: absolute;
  width: 100%;
  height: 3px;
  @media ($md) {
    height: 6px;
  } 
}

@mixin visually-hidden {
	position: absolute !important;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
	clip: rect(1px, 1px, 1px, 1px);
	padding:0 !important;
	border:0 !important;
	height: 1px !important; 
	width: 1px !important; 
	overflow: hidden;
}

@mixin gradient {
	background: linear-gradient(45deg, $grad-color-from, $grad-color-to);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;  
}


