.popup {
  padding:5px 15px 15px;
  position:fixed;
  top:100px;
  left:50%;
  display:none;
  overflow:hidden;
  border:1px solid $button-color;
  background:#fff;
  -moz-border-radius:15px;
  -webkit-border-radius:15px;
  z-index:100;

  &__title {
    font:bold 18px/32px Arial, san-serif;
  }

  &__close {
    width:16px;
    height:16px;
    display:block;
    text-indent:-9999px;
    position:absolute;
    top:10px;
    right:10px;
    background:url(../img/close.png) no-repeat;
  }
}

.callback_form {
  margin-left:-150px; /*половина от, выровненного на 50% слева, блока */
  width:300px;
  background: linear-gradient(45deg, $grad-color-from, $grad-color-to);
  @media ($md) {
    width: 400px;
    margin-left: -200px;
  }
  &__form {
    margin-top:10px;
    }
    &__label {
    width:80%;
    height:26px;
    font:bold 12px/26px Arial, san-serif;
    display:block;
    vertical-align:top;
    }
    
    &__input {
    display: inline-block;
    margin-bottom:10px;
    padding:0 3px;
    width:80%;
    height:22px;
    font:bold 12px/26px Arial, san-serif;
    border:1px solid $button-color;

      &_submit {
        margin:10px 15px 0 0;
        padding:3px 10px;
        float:right;
        background:$button-color;
        border: 1px solid #fff;
        -moz-border-radius:3px;
        -webkit-border-radius:3px;
        border-radius:3px;
        font:bold 10px Arial, san-serif;
        text-transform:uppercase;
        position:relative;
        cursor:pointer;

        &:hover {
          color:#fff;
          }
      }
    }
}

  .overlay {
    width:100%;
    height:100%;
    position:fixed;
    top:0;
    left:0;
    display:none;
    background:#000;
    opacity:.8;
    z-index: 99;
    }
