
  /* сгенерированные icon-fonts из своих SVG файлов */
  @font-face {
    font-family: 'icons-generated';
    src:  url('../fonts/icons-generated.woff?tngna8') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  }
    
  [class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icons-generated' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  .icon-call-answer:before {
    content: "\e900";
  }
  .icon-ellipse:before {
    content: "\e901";
  }
  .icon-envelope:before {
    content: "\e902";
  }
  .box0 {
    border: 1px solid red;
  }