.flex-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.central-container {
  margin: 0 auto;
	padding: 0 20px;
  width: 320px;
  @media ($md) {
    width: 1024px;
    padding: 0 40px;
  }
  @media ($lg) {
    width: 1220px;
    padding: 0 25px;
  }
}

.header {
	padding-top: 25px;
	background-color: $secondary-background-color;
	position: relative;
	@media ($md){
		padding-top: 30px;
	}
}


.tel-order-container {

	&__tel-top {
		font-size: 18px;
		font-weight: bold;
		display: inline-block;
		vertical-align: middle;
		@media ($md) {
			font-size: 25px;
		}
	}
}


.ellipse-pic {
	background-image: url(../img/ellipse.svg);
	background-repeat: no-repeat;
	background-size: 475px auto;
	background-position: 34% 118px;
	position: relative;
	overflow: hidden;
	@media ($md) {
		background-size: 824px auto;
    background-position: 480px 163px;
	}
	@media ($lg) {
		background-size: 950px auto;
    background-position: 114% 65px;
	}

	&__picture {
		display: flex;
		padding-left: 22%;
		padding-top: 85px;
		@media ($md) {
			padding-left: 54%;
			padding-top: 58px;
		}
		@media ($lg) {
			padding-left: 62%;
			padding-top: 58px;
		}
	}

	&__img {
		object-fit: cover;
		height: 328px;
		width: 236px;
		@media ($md) {
			height: 636px;
			width: 435px;
		}
		@media ($lg) {
			filter: drop-shadow(10px 5px 10px #000);
		}
	}
}

