.name-more-container {
	padding-top: 12px;
	@media ($md) {
		position: absolute;
    top: 309px;
    background-color: unset;
    padding-top: unset;
	}
	@media ($lg) {
		top: 76px;
	}

	&::after {
		position: absolute;
		content: '';
		bottom: -1px;
		left: 0;
		width: 100%;
		height: 183px;
		background-color: $main-background-color;
		z-index: 0;
		@media ($md) {
			display: none;
		}
	}

	&__name {
		margin: 0 0 22px 0;
		font-size: 30px;
		text-align: center;
		font-weight: bold;
		position: relative;
		z-index: 1;
		@media ($md) {
			font-size: 57px;
			letter-spacing: -0.03em;
			text-align: unset;
			margin: 0 0 40px 0;
		}
		@media ($lg) {
			font-size: 85px;
			padding-top: 207px;
		}
	}
	
	&__name-specialty {
		display: block;
		font-weight: normal;
		color: #919bb3;
		text-align: center;
		margin-top: 8px;
		padding: 0 20px;
		line-height: 1.5em;
		font-size: 16px;
		@media ($md) {
			font-size: 25px;
			margin-top: 19px;
			padding-left: 0;
			padding-right: 0;
			letter-spacing: 0.05px;
			text-align: unset;
			font-weight: normal;
		}
		@media ($lg) {
			margin-top: 8px;
			font-size: 25px;
		}
	}
}
