.slider {
  overflow: hidden; /* чтобы не вылазило за поля ЦК*/

  &__wrapper {
    display: flex;
    // flex-wrap: wrap; /* перенос невлезающих блоков*/
    transition: transform 0.6s ease;
    // justify-content: space-between;
    // overflow: hidden;/* скрытие этих невлезающих блоков*/
    // height: 270px;
  }
  
  &__item {
    flex: 0 0 100%;
    width: 100%;
    @media ($md) {
      flex: 0 0 50%;
      width: 50%;
      padding: 0 10px;
    }
    @media ($lg) {
      flex: 0 0 33.333%;
      width: 33.3333%;
      padding: 0 12px;
    }
  }

  &__content {
    @include reset;

    &_header {
      @include reset;
    }
    
    &_title {
      font-size: 17px;
      color: #8993ad;
      padding: 15px 0;
      margin: 0;
      text-align: center;
      @media ($md) {
        font-size: 25px;
      }
      @media ($lg) {
        font-size: 20px;
        padding: 30px 0;
      }
    }

    &_img {
    width: 100%; 
    }
  }



  /* стрелки -----------------------------------*/

  &__control {
    display: none;
    position: absolute;
    background-image: url(../img/arrow.png);
    background-position: center;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border-style: none;
    box-shadow: 0 0 15px black;
    top: 50%;
    @media ($lg) {
      display: block;
    }
    
    &:hover {
      box-shadow: 0 0 15px red;
    }
    
    &_left {
      left: 0;
    }
    
    &_right {
      right: 0;
      transform: rotate(180deg);
    }
  }

  /* Точки ----------------------------------------*/

  &__indicators {
    @include reset;
    display: flex;
    justify-content: center;
    padding: 0;
    list-style: none;
    margin-top: 15px; 
    @media ($md) {
      margin-top: 75px;
    }
    @media ($lg) {
      display:none;
    }

    & li { /* переделать под БЭМ, но т.к. надо залезать в скрипт то оставляю так*/
      box-sizing: content-box;
      flex: 0 1 auto;
      width: 11px;
      height: 11px;
      border-radius: 50%;
      margin-right: 8px;
      margin-left: 8px;
      text-indent: -999px;
      cursor: pointer;
      background-color: $button-color;
      background-clip: padding-box;
      border-top: 10px solid transparent;
      border-bottom: 10px solid transparent;
      opacity: .5;
      transition: opacity .6s ease;
      @media ($md) {
        width: 18px;
        height: 18px;
        margin-right: 15px;
        margin-left: 15px;
      }
      &.active {
        opacity: 1;
      }
    }
  }
}

.arrow-wrapper {
	position: relative; /*для кнопок = container*/
}