
  /* для нормальной жирности: */
  @font-face {
    font-weight: 400;/* жирность в цифрах = normal*/
    font-style: normal;/* стиль наклона */
    font-family: "Roboto";/* семейство фрифтов / имя шрифта*/
    font-display: swap;/* при загрузке страницы сначала загружается системный шрифт а потом стилизованный.*/
    src: 
      local("Roboto"), /* если шрифт уже установлен в системе то использование его, а если нет то берет из папки ниже*/
      url("../fonts/Roboto.woff2") format("woff2"),/*стороннего шрифта a формате woff2*/
      url("../fonts/Roboto.woff") format("woff");/*стороннего шрифта a формате woff*/
  }
  
  
  
  /* для жирного начертания: */
  @font-face {
    font-weight: 600;/* жирность в цифрах*/
    font-family: "Roboto";
    font-display: swap;
    src: 
      local("Roboto-bold"), 
      url("../fonts/Roboto-bold.woff2") format("woff2"),
      url("../fonts/Roboto-bold.woff") format("woff");
  
  }
