.scrollup {
  position: fixed;
  right: 20px;
  bottom: 50px;
  width: 60px;
	height: 60px;
	z-index: 20;
  border: 2px dotted $button-color;
  border-radius: 50%;
  background-image: url(../img/arrow.png);
  background-repeat: no-repeat;
  background-position: center;
  transform: rotate(90deg);
  display: none;
}

