.footer {
	margin-top: 45px;
	@media ($md) {
		margin-top: 106px;
	}
	@media ($lg) {
		margin-top: 80px;
	}

	&__content {
		padding-bottom: 43px;
		display: flex;
		flex-flow: column nowrap;
		@media ($md) {
			flex-flow: row wrap;
			justify-content: space-between;
			padding-bottom:0;
			margin-bottom: 50px;
		}
	}
}

.contacts {
	&_footer {
		margin-bottom:25px ;
		margin-right: 30px;
	}

	&__list {
		@include reset;
	}

	&__item {
		display: flex;
		align-items: center;
		margin-bottom: 24px;
		@media ($md) {
			padding-left: 50px;
			margin-bottom: 29px;
		}
		@media ($lg) {
			padding-left: 90px;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__link {
		color: #919bb3;
		padding-left: 20px;/*место для иконки*/
		background-repeat: no-repeat;
		background-position-y: 50%;
		background-size: 14px 14px;
		font-style: normal;
		font-size: 20px;
		@media ($md) {
			color: black;
			font-size: 25px;
			background-size: 20px 20px;
		}

		&_tel-bottom {
			background-image: url('../img/call-answer.svg');
		}	
		
		&_e-mail {
			background-image: url('../img/envelope.svg');

		}
	}
}

.icon-footer {
	font-size: 13px;
	@include gradient;
	@media ($md) {
		font-size: 20px;
	}
}

.social {
	margin-top: 47px;
	@media ($md) {
    margin-top: 23px;
	}

	&__list {
		@include reset;
	}
	
	&__item {
		display: inline-block;
		padding-right: 13px;
	}
	
	&__link {
		display: block;
		width: 35px;
		height: 35px;	
		background-image: url(../img/sprite.png);
		background-repeat: no-repeat;
	
		&_fb {
			background-position: 100% 93%;
		}
		
		&_vk {
			background-position: 23% 80%;
		}
			
		&_twit {
			background-position: -5% -7%;
		}
	
		&_inst {
			background-position: 70% 11%;
		}
		
		&:hover {
			filter: drop-shadow(0 0 5px #000);
		}
	}
}

.more-social-container {
	@media ($lg) {
		padding-right: 20px;
	}
}

.black-bar-bottom {
	@include reset;
	text-align: center;
	padding: 19px;
	width: 100%;
	justify-content: center;
	color: white;
	background-color: #31293c;
	font-size: 12px;
	@media ($md) {
		padding: 30px;
    font-size: 16px;
	}
}

