.button-link {
  display: block;
	text-align: center;
	background-color: $button-color;
  color: $main-background-color;
  
	&:hover {
		filter: drop-shadow(0 0 5px #000);
	}

  &_tel-order-container {
    display: inline-block;
		vertical-align: middle;
		margin-left: 25px;
		font-size: 0;
		width: 30px;
		padding-bottom: 30px;
		background-image: url('data:image/png;base64,');
		background-repeat: no-repeat;
		background-position: center;
		background-color: transparent;
		@media ($md) {
			font-size: 16px;
			color: $button-color;
			width: 207px;
			background-image: none;
			border: 1px solid $button-color;
			padding-top: 14px;
			padding-bottom: 10px;
			background-color: unset;
		}
  }

  &_name-more-container {
		padding: 18px 20px;
		position: relative;
		z-index: 1;
		@media ($md) {
			width: 320px;
			padding: 23px 0;
			font-size: 18px;
			margin: 0;
		}
		@media ($lg) {
			width: 270px;
			padding: 20px 0;
		}
  }

  &_do {
    width: 280px;
    padding: 18px 20px;
    margin-top: 53px;
    @media ($md) {
      margin: 95px auto 0 auto;
      width: 320px;
      font-size: 18px;
      padding: 24px 0;
    }
    @media ($lg) {
      margin-top: 15px;
      width: 270px;
      padding: 19px 0;
    }
  }

  &_examples {
    margin: 0 auto;
    margin-top: 40px;
    width: 280px;
    padding: 18px 20px;
    @media ($md) {
      width: 320px;
      padding: 25px 20px;
      margin-top: 90px;
    }
    @media ($lg) {
      width: 270px;
      padding: 20px 20px;
      margin-top: 60px;
    }
  }

  &_more-social-container {
    width: 280px;
    padding: 18px 20px;
    @media ($md) {
      color: $button-color;
      border: 1px solid $button-color;
      background-color: unset;
      width: 247px;
      font-size: 16px;
      padding: 13px 0;	
    }
    @media ($lg) {
      padding: 12px 0;
      width: 250px;
    }
  }
}