.top-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.menu {
	margin: auto 0;
	position: relative; /* позиция для меню*/

	&_footer {
		margin-bottom: 40px;
		margin-right: 30px;
	}
	
	/* стилизуем кнопку */
	&__btn {
		display: flex; /* используем flex для центрирования содержимого */
		align-items: center;  /* центрируем содержимое кнопки */
		position: absolute; /* абсолют , иначе крестика не будет видно на фоне расрытого меню */
		top: -3px;
		left: 0;
		width: 24px;
		height: 26px;
		cursor: pointer;
		z-index: 3; /* иначе при откр меню эта кнопка останется позади */
		@media ($md) {
			width: 44px;
		}
		@media ($lg) {
		display: none;
		}
	}
	
	/* рисуем 3 элемента "бургера" */
	&__btn > span {
		@include burger-icon-incommon;
		background-color: ($grad-color-from + $grad-color-to)/2;
		transition-duration: .25s;
		
		&::before{
			@include burger-icon-incommon;
			content: '';
			top: -8px;
			background-color: $grad-color-to;
			transition-duration: .25s;
			@media ($md) {
				top: -13px;
			}
		}
		
		&::after {
			@include burger-icon-incommon;
			content: '';
			top: 8px;
			background-color: $grad-color-from;
			transition-duration: .25s;
			@media ($md) {
				top: 13px;
			}
		}
	}

	/* контейнер меню */
	&__list {
		position: fixed;
		top: 0;
		left: -100%;
		visibility: hidden;
		width: 200px;
		margin: 0;
		padding: 80px 0;
		list-style: none;
		text-align: center;
		background-color: $button-color;
		box-shadow: 1px 0px 6px rgba(0, 0, 0, .9);
		z-index: 2;
		transition-duration: .25s;
		@media ($md) {
			padding-top: 120px;
		}
		@media ($lg) {
			display: inline;
			position: unset;
			visibility: unset;
			padding: 0;
			background: unset;
			box-shadow: unset
		}

		&_footer {
			@include reset;
		}
	}

	/* элементы меню */
	&__item {
		display: block;
		padding: 12px 24px;
		color: #333;
		font-size: 20px;
		font-weight: bold;
		text-decoration: none;
		transition-duration: .25s;
		@media ($lg) {
			display: inline;
			margin-right: 25px;
			font-size: 16px;
			font-weight: unset;
			padding: 0;
			background: unset;
		}

		&:hover {
			background-color: gainsboro;
			@media ($lg) {
				background:none;
				border-bottom: 2px solid $button-color;
				color: $button-color;
			}
		}

		&_footer {
			font-size: 15px;
			color: #919bb3;
			margin-bottom: 12px;
			line-height: 1.4em;
			@media ($md) {
				font-size: 18px;
				line-height: unset;
			}
			
			&:last-child {
			margin-bottom: 0;
			}
		}
	}
}

/* скрываем чекбокс */
#menu__toggle {
  opacity: 0; /* иначе, останется стандартный квадратик с галочкой*/
  @media ($lg) {
    display: none; /* иначе, останется само место под квадратик с галочкой*/
		}
	
	/* действие открытия и закрытия меню */
	&:checked ~ .menu__btn{
		position: fixed;
		top: 30px;
		left: 150px;	
		@media ($md) {
			left: 130px;
		}
	}

	&:checked ~ .menu__btn > span {
		transform: rotate(45deg);
	
		&::before {
			top: 0;
			transform: rotate(0);
			background-color: $grad-color-from;
		}

		&::after {
			top: 0;
			transform: rotate(90deg);
		}
	}

	&:checked ~ .menu__list {
		visibility: visible;
		left:0;
	}
}